import React from 'react';
import {Button, Form, Input} from "antd";
import {Link, useNavigate} from "react-router-dom";
import toast from "react-hot-toast";
import axios from "axios";

const Register = () => {

    const navigate = useNavigate();
    const onFinish = async (values) => {
        try {
            const response = await axios.post('/api/users/login', values);
            if (response.data.success) {
                toast.success(response.data.message);
                toast("Redirecting you to the home page");
                localStorage.setItem("token", response.data.data);
                navigate("/");
            } else {
                toast.error(response.data.message);
            }

        } catch (error) {
            toast.error("Something went wrong");
        }
    };
    return (
        <div classsName='authentication'>
            <div className='authentication-form card p-2'>
                <h1 className='card-title'>WELCOME BACK</h1>

                <Form layout='vertical' onFinish={onFinish}>

                    <Form.Item label='Student number' name='studentNumber'>
                        <input placeholder='Please enter your student number'/>
                    </Form.Item>

                    <Form.Item label='Password' name='password'>
                        <input placeholder='Please enter your password' type='password'/>
                    </Form.Item>


                    <Button className='primary-button my-2' htmlType='submit'>LOGIN</Button>
                    <Link to='/Register' className='anchor mt-2'> Click Here To Register</Link>


                </Form>
            </div>


        </div>
    );
};

export default Register;