import React from 'react';
import {Button, Form, Input} from "antd";
import axios from "axios";
import toast from "react-hot-toast";
import {Link, useNavigate} from "react-router-dom";

const Register = () => {
    const navigate = useNavigate();
    const onFinish = async (values) => {
        try {
            const response = await axios.post('/api/user/register/', values);
            if (response.data.success) {
                toast.success(response.data.message);
                toast("Redirection to the login page");
            } else {
                toast.error(response.data.message);
                navigate ("/login");
            }


        } catch (error) {
            toast.error("Something went wrong");
        }

    }
    return (
        <div classsName='authentication'>
            <div className='authentication-form card p-2'>
                <h1 className='card-title'>REGISTER HERE</h1>

                <Form layout='vertical' onFinish={onFinish}>
                    <Form.Item label='Name' name='name'>
                        <input placeholder='Please enter your name'/>
                    </Form.Item>

                    <Form.Item label='Student number' name='studentNumber'>
                        <input placeholder='Please enter your student number'/>
                    </Form.Item>

                    <Form.Item label='Password' name='password'>
                        <input placeholder='Please enter your password'/>
                    </Form.Item>


                    <Button className='primary-button my-2' htmlType='submit'>REGISTER</Button>
                    <Link to='/Login' className='anchor mt-2'> Click Here To Login</Link>


                </Form>
            </div>


        </div>
    );
};

export default Register;